@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro:wght@500&display=swap);
.HomePage {
    width: 100%;
    height: 100%;
    min-height: calc(100vh - 110px) !important;
}

.HomePage .ContentArea {
    margin: 25px 0;
}

.HomePage .downloadLatestVersion {
    display: flex;
}

@media (max-width: 1000px) {
    .HomePage .downloadLatestVersion .ImageLabel {
        width: 150px !important;
        height: 150px !important;
    }
}

@media (max-width: 600px) {
    .HomePage .downloadLatestVersion .ImageLabel {
        display: none;
    }
    .HomePage .downloadLatestVersion .infoArea {
        margin-left: unset !important;
    }
    .HomePage .downloadLatestVersion .ButtonArea {
        margin: 0 10px 0 0;
    }
    .HomePage .downloadLatestVersion .ButtonArea a {
        width: 100%;
    }
    .HomePage .downloadLatestVersion .ButtonArea .LinkButton {
        margin: 5px 0;
        width: auto;
    }
    .HomePage .downloadLatestVersion .ButtonArea .Button {
        margin: 5px 0;
    }
}

.HomePage .downloadLatestVersion .ButtonArea {
    margin: 20px 0 0 auto;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.HomePage .downloadLatestVersion .ButtonArea .LinkButton {
    margin: 5px;
    padding: 15px 35px;
    font-size: 30px;
    text-align: center;
}

.HomePage .downloadLatestVersion .ButtonArea .downloadButton {
    margin: 5px;
    padding: 15px 35px;
    font-size: 30px;
    color: white;
    background-color: #00cf00;
    text-decoration: none;
    text-align: center;
}

.HomePage .downloadLatestVersion .downloadButton:hover {
    background-color: #00b400;
}

.HomePage .downloadLatestVersion .downloadButton:active {
    background-color: #008600;
}

.HomePage .features {
    font-size: 15pt;
    font-family: 'Noto Sans KR', sans-serif !important;
}

.HomePage .faq .ContentSubtitle {
    margin-bottom: 10px;
}

.HomePage .faq .ContentTextArea {
    font-family: 'Noto Sans KR', sans-serif !important;
}
.PageFrame {
    margin: 0 auto;
    padding: 10px;
    width: 100%;
    max-width: 1200px;
}
.ContentArea {
    padding: 20px 10px;
    background-color: #ffffff;
    border: 1px solid #e7eaec;
    border-top: 3px solid #e5e5e5;
    border-radius: 5px;
}
.ContentBox {
    margin: 10px;
    padding: 10px;
    background-color: #f3f4f5;
    border: 1px solid #e0e2e4;
    border-radius: 5px;
    font-family: 'Nanum Gothic', sans-serif;
}
.ContentSubtitle {
    margin-bottom: 20px;
    padding: 10px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    border-bottom: 2px solid #000000;
    font-size: 16pt;
    font-family: 'Nanum Gothic', sans-serif;
}
.ContentTextArea {
    padding: 5px 10px;
    font-size: 12pt;
}

.ContentTextArea p {
    margin-bottom: 16px;
}
.ContentTitleBar {
    margin: 30px 0;
    padding: 30px;
    background-color: #ffffff;
    border: 1px solid #e7eaec;
    border-top: 3px solid #eea3aa;
    border-radius: 5px;
    font-size: 15pt;
}
.ContentSrcViewer {
    margin: 10px;
    padding: 15px;
    color: #f8f8f2;
    background-color: #272822;
    font-family: 'Source Code Pro', monospace;
    word-break: break-all;
}
.HashTag {
    display: inline-block;
    margin: 2px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding: 0 5px;
    font-size: 17px;
    background-color: rgba(150, 150, 150, 0.5);
    border-radius: 5px;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    cursor: pointer;
}

.HashTag:hover {
    background-color: rgba(150, 150, 150, 0.8);
}
